<template>
  <v-container fill-height class="d-flex justify-center align-center">
    <!-- <v-card width="200px" flat :class="`d-flex justify-center  image_card`">
      <v-img :src="require('../../static/images/纸飞机.png')" width="150px" contain></v-img>
    </v-card>-->
    <v-card flat>
      <v-text-field
        label="输入川大学生邮箱"
        prepend-icon="mdi-email-box"
        v-model="email"
        :rules="[filter]"
      >
        <template v-slot:append>
          <v-btn
            color="primary"
            small
            text
            @click.stop="loginIntroDialog.show = true"
            class="mb-1"
          >
            <span>邮箱登陆指南</span>
          </v-btn>
        </template>
      </v-text-field>
      <div class="d-flex ">
        <v-text-field
          label="输入验证码"
          prepend-icon="mdi-barcode"
          v-model="code"
        >
          <template v-slot:append>
            <v-btn
              color="primary"
              small
              text
              class="pb-2"
              @click="getCode"
              v-if="!oneMinuteInterval"
              >点击获取验证码</v-btn
            >
            <v-btn
              color="primary"
              small
              text
              class="pb-2"
              v-if="oneMinuteInterval"
              disabled
              >{{ cnt }}秒后重发</v-btn
            >
          </template>
        </v-text-field>
        <v-btn
          color="primary"
          class="mt-4"
          icon
          small
          @click="loginProblem.show = true"
        >
          <v-icon>mdi-help-circle-outline</v-icon>
        </v-btn>
      </div>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" outlined>
          <span class="text_xsmall" @click="toSCUMail">前往四川大学邮箱</span>
        </v-btn>
        <v-btn color="primary" @click="registerWithEmail">
          <v-icon right>mdi-arrow-right-bold-circle</v-icon>
          <span class="pr-2">登陆</span>
        </v-btn>
      </v-card-actions>
    </v-card>

    <v-dialog
      v-model="loginIntroDialog.show"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636" >
        <v-card-title>
          <v-icon>mdi-login</v-icon>
          <span class="ml-1">邮箱登陆指南</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-1">
          <v-carousel
            cycle
            height="400"
            hide-delimiter-background
            show-arrows-on-hover
          >
            <v-carousel-item
              v-for="(item, i) in loginIntroDialog.intro"
              :key="i"
            >
              <v-img
                :src="require(`../../static/images/loginIntro/${i + 1}.png`)"
                contain
                max-height="300px"
              ></v-img>
              <div v-html="item"></div>
            </v-carousel-item>
          </v-carousel>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="loginProblem.show"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-help-circle-outline</v-icon>
          <span class="ml-2">登陆常见问题</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-1">
          <div>
            <li>
              <strong>Q：收不到验证码怎么办？</strong>
            </li>
            <span
              >A：请检查邮箱名是否输入正确，如果正确，请多刷新几次，检查邮件垃圾箱中是否有验证码。</span
            >
            <li>
              <strong>Q：邮箱无法登陆？</strong>
            </li>
            <span
              >A：请确认四川大学邮箱是否激活了，如果没有激活，请按照“邮箱输入栏”中的“邮箱登陆指南”步骤激活邮箱</span
            >
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-snackbar
      v-model="snackbar.show"
      :color="snackbar.type == 'ok' ? 'red lighten-2' : 'grey darken-3'"
      :timeout="1100"
      centered
    >
      {{ snackbar.message }}
    </v-snackbar>
  </v-container>
</template>
<script>
import qs from "qs";
import isNull from "@/utils/isNull";
export default {
  name: "login",
  data() {
    return {
      clientHeight: document.documentElement.clientHeight,
      loginIntroDialog: {
        show: false,
        intro: [
          ' <div class="mt-1"  >  <li >用户名默认为学号或者姓名全拼</li> <li>密码为自定义的6-18位密码</li> <li><strong>第一次使用邮箱需要激活，激活步骤请继续看下一张卡片</strong></li></div>',
          '    <div class="mt-1"> <li>学校选择<strong>四川大学学生邮箱</strong></li> <li>默认密码为stu+身份证后六位</li> </div>',
          ' <div class="mt-3"> <li>请绑定常用手机</li> </div>',
          '   <div class="mt-3"> <li>设置密码后点击激活即可</li> </div>'
        ],
        introImages: [
          "../../static/images/loginIntro/1.png",
          "../../static/images/loginIntro/2.png",
          "../../static/images/loginIntro/3.png",
          "../../static/images/loginIntro/4.png"
        ]
      },
      loginProblem: {
        show: false
      },
      ipProblemDialog: false,
      email: "@stu.scu.edu.cn",
      code: "",
      oneMinuteInterval: false,
      cnt: 20,
      snackbar: {
        show: false,
        message: "",
        type: "error"
      },
      filter: v => {
        console.log(this.whiteList);
        var pattern = /^([A-Za-z0-9_\-\.\u4e00-\u9fa5])+@stu.scu.edu.cn/;
        return pattern.test(v) || this.whiteList.includes(v)
          ? true
          : "邮箱不合法";
      },
      whiteList: []
    };
  },
  created() {
    let that = this;
    that.$request("get", "/user/getWhiteList", {}, {}).then(res => {
      that.whiteList = res.data.map(item => item.email);
    });
  },
  mounted() {
    window.onresize = () => {
      this.clientHeight = document.documentElement.clientHeight;
    };
  },
  methods: {
    showSnackbarInLogin(m, type = "error") {
      this.snackbar = {
        show: true,
        message: m.message || m,
        type: type
      };
    },
    registerWithEmail() {
      let that = this;
      if (isNull(this.code)) {
        that.showSnackbarInLogin("验证码不能为空");
        return;
      }
      that
        .$request(
          "post",
          "/user/registerWithEmail",
          {},
          {
            email: that.email,
            code: that.code
          }
        )
        .then(res => {
          that.showSnackbarInLogin(res, "ok");
          localStorage.setItem("token", res.data);
          setTimeout(async () => {
            // await that.setConfig()
            that.$router.replace("/main/index");
          }, 1000);
        })
        .catch(e => {
          that.showSnackbarInLogin(e);
        });
    },
    getCode() {
      let that = this;
      if (this.filter(this.email) == "邮箱不合法") {
        that.showSnackbarInLogin("邮箱不合法");
        return;
      }
      that
        .$request("post", "/user/getCode", {}, { email: this.email })
        .then(res => {
          that.showSnackbarInLogin(res || "验证码已发送到邮箱,请查收", "ok");
        })
        .catch(e => {
          that.showSnackbarInLogin(e);
        });
      this.oneMinuteInterval = true;
      let interval = setInterval(() => {
        this.cnt--;
        if (this.cnt == 0) {
          this.oneMinuteInterval = false;
          clearInterval(interval);
          this.cnt = 20;
        }
      }, 1000);
    },

    toSCUMail() {
      window.open(
        "http://mail.stu.scu.edu.cn/coremail/index.jsp?nodetect=true",
        "_system",
        {}
      );
    }
  },
  watch: {
    ipProblemDialog(v) {
      this.$store.change_dialog(v, "ipProblemDialog");
    },
    "$store.state.dialog"(v) {
      if (this.$store.state.dialogName == "ipProblemDialog")
        this.ipProblemDialog = v;
    }
  }
};
</script>

<style>
.image_card {
  margin-top: -120px;
}
</style>



